import { DEFAULT_AVATAR } from './../../../services/constants';
import { PageBusiness } from './../../page.d';
import { Utility } from './../../../services/utility';
import { TranslateService } from '@ngx-translate/core';
import { SocialProvider } from './../../../providers/social/social';
import { AchievePage } from './../achieve-page';
import { Session } from './../../../services/session';
import { WidgetProvider } from './../../../providers/widget-provider';
import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, Content, Events } from 'ionic-angular'; 
import { Webpage } from '../../../models/models';
import { NativeService } from '../../../services/services';

/**
 * Generated class for the MyAssetsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-my-assets',
  templateUrl: 'my-assets.html',
})
export class MyAssetsPage extends AchievePage implements PageBusiness{

  dailyLists: any[] = [];
  scoreHistory: any[] = [];
  achieveTtem: string = 'score';
  rankButtons: any[] = []
  rankData: any[] = []; 
  displayList: any[] = [] 
  showMass: boolean = false;
  lastButton: any;
  rankCode: string = ''
  scoreItem: any;
  isDataLoading: boolean = false; //是否在加载数据
  isDataEmpty: boolean = false; // 是否为无数据 
  page: number = 1; // 页码
  pageCount: number = 20; // 每页条数
  hasMoreData: boolean = false;
  myRankPositionItem: any;
  @ViewChild(Content) content: Content; 
  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    private widgetProvider: WidgetProvider,
    public session: Session,
    public nativeService: NativeService,
    public socialProvider: SocialProvider,
    private translate: TranslateService,
    private events: Events) {
      super(session, nativeService)
      this.achieveTtem = this.navParams.data.selected || 'score';
      this.scoreItem = this.navParams.data.details ;
      this.CowryDetail()
      this.getScoreHistory()
      this.rankButtons = [
      //   {
      //   rankName: this.translate.instant('SOCIAL_MY_DAILY_LIST'),
      //   rankCode: 'dayList',
      //   myRankCode: 'myDayList',
      //   selected: true
      // },
      {
        rankName: this.translate.instant('SOCIAL_MY_WEEK_LIST'),
        rankCode: 'weekList',
        myRankCode: 'myWeekList',
        selected: true,
        offsetTop: 0
      },{
        rankName: this.translate.instant('SOCIAL_MY_MONTH_LIST'), 
        rankCode: 'monthList',
        myRankCode: 'myMonthList',
        offsetTop: 0
      },{
        rankName: this.translate.instant('SOCIAL_MY_TOTAL_LIST'),
        rankCode: 'totalList',
        myRankCode: 'myTotalList',
        offsetTop: 0
      }]
      this.lastButton = this.rankButtons[0]

      this.myRankPositionItem = {
        avator: this.session.user.pictureUrl || DEFAULT_AVATAR,
        name: this.session.user.nickName || this.session.user.userName,
        level: this.scoreItem.level,
        ranking: '1',
        score: '-',
        levelIcon:''
      }
      this.showMass = this.session.user && Utility.boolean(this.session.user.showMass)
  }
  ionViewDidLoad(){
    this.events.subscribe('achievement:refresh-assets', () => {  
      this.CowryDetail()
      this.getScoreHistory()
    })  
  }

  ionViewWillUnload() {
    //console.log('界面销毁');
    this.events.unsubscribe('achievement:refresh-assets'); 
  }
 
  /**
   * 积分历史颜色
   * @param _item 
   * @param position 
   */
  littePointColor(_item, position){
    if(position == 'text'){  
        return { color: _item.typeForeColor}   
    }else{ 
      return { backgroundColor: _item.typeForeColor}  
    } 
  }

  /**
   * 选择排名类型
   * @param _button 
   */
  rankOption(_button){ 
    for(let item of this.rankButtons){
      if(this.lastButton.rankCode == item.rankCode){
        item.offsetTop = this.content.scrollTop;
      }
    }
    for(let item of this.rankButtons){ 
      if(_button.rankCode == item.rankCode){
        this.lastButton = item ;
        this.rankCode = item.rankCode
        item.selected = true  
        this.myRankPositionItem = this.rankData[item.myRankCode].length > 0 ? this.rankData[item.myRankCode][0] : this.myRankPositionItem 
        setTimeout(() => {
          this.content.scrollTo(0, item.offsetTop, 0);
        }, 1)
        this.iconCheck()
      }else{
        item.selected = false
      }
    } 
  }

  /**
   * 链接到任务
   * @param item 
   */
  goLink(item){ 
    this.nativeService.setStatusBarTextDark(this.session.isDarkMode)
    let page: Webpage ={
      url: item.redirectURL,
      appId:item.appId,
      options: {} 
    }
    this.widgetProvider.openWebpage(page,this.navCtrl)
  }

  /**
   * 获取每日任务、排名
   */
  CowryDetail(){
    this.socialProvider.CowryDetail().subscribe(res=>{ 
      this.dailyLists = res.result.tasks; 
      this.rankData = res.result.rankings; 

      for(let i = 0; i < this.rankButtons.length; i++ ){
        let ranklist = {
          type: this.rankButtons[i].rankCode,
          data: this.rankData[this.rankButtons[i].rankCode]
        }
        this.displayList.push(ranklist)
      }
      let icons = ['assets/img/rank/1.png','assets/img/rank/2.png','assets/img/rank/3.png'] ; 
      for(let items of this.displayList){
        for(let item of items.data){
          if(!item.avator)item.avator = DEFAULT_AVATAR;
          let itemRank = Number(item.ranking) - 1
          if(itemRank >= 0 && itemRank < 3){
            item.rankIcon = icons[itemRank]
          }
        } 
      }
      this.rankCode = this.rankButtons[0].rankCode;
      let myRankCode = this.rankButtons[0].myRankCode; 
      this.myRankPositionItem = this.rankData[myRankCode].length > 0 ? this.rankData[myRankCode][0] : this.myRankPositionItem  
      this.iconCheck()
    })
  }
   
  iconCheck(){
    let icons = ['assets/img/rank/1.png','assets/img/rank/2.png','assets/img/rank/3.png'] ; 
    let myCurrentRank = Number(this.myRankPositionItem.ranking)-1 ;
    if(myCurrentRank >= 0 && myCurrentRank < 3){
      this.myRankPositionItem.rankIcon = icons[myCurrentRank]
    } 
  }
  
  ionViewWillEnter(){ 
    super.ionViewWillEnter()
  }
  ionViewWillLeave() {  
    super.ionViewWillLeave() 
  }

  leaderboard(){
    this.navCtrl.push('LeaderBoardPage')
  } 

  /**
   * 获取积分历史
   */
  getScoreHistory(){
    this.socialProvider.ScoreHis(this.page,this.pageCount).subscribe(res=>{ 
      this.scoreHistory = res.result;
      res && res.result && (this.hasMoreData = res.result.length == this.pageCount );
      for(let item of this.scoreHistory){ 
        item.taskTime =  Utility.dateFormat(new Date(item.taskTime) || new Date())
      }
    })
  }

  /**
   * 更多积分历史
   * @param _event 
   */
  moreScoreHistroy(_event){
    this.page ++;
    this.socialProvider.ScoreHis(this.page,this.pageCount,true).subscribe(res=>{  
      res && res.result && (this.hasMoreData = res.result.length == this.pageCount );  
      for(let item of res.result){ 
        item.taskTime =  Utility.dateFormat(new Date(item.taskTime) || new Date())
      }
      this.scoreHistory.push.apply(this.scoreHistory, res.result);
      _event && _event.complete()
    }, err=>{
      _event && _event.complete()
    }) 
  }

  isRankNumber(rank: string){
    let number = Number(rank);
    return !isNaN(number) && number > 0;
  }
 
}



